<template>
  <div id="user_add">
    <Modal
      v-model="build"
      title=""
      @on-cancel="AddData = {}"
    >
      <Form ref="add" :model="AddData" :rules="AddRule" :label-width="90" style="padding:0 50px;">
        <FormItem prop="addRealName" label="">
          <Input v-model.trim="AddData.addRealName" placeholder="" size="small"/>
        </FormItem>
        <FormItem prop="addPhoneNumber" label="">
          <Input v-model.trim="AddData.addPhoneNumber" placeholder="" size="small"/>
        </FormItem>
        <FormItem prop="addCode" label="" >
            <Cascader :data="codeData" v-model="AddData.addCode" size="small" change-on-select></Cascader>
        </FormItem>
        <FormItem prop="addDepartment" label="">
            <Select v-model="AddData.addDepartment" size="small">
              <Option v-for="item in departmentList" :value="item.id" :key="item.id">{{ item.name }}</Option>
            </Select>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="addSubmit"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { sysMobileUserAdd } from '@/api/systemManage'
import { getToken } from '@/utils/auth'
export default {
  name: 'UserAdd',
  data () {
    return {
      /*  */
      AddData: {
        addRealName: '',
        addPhoneNumber: '',
        addCode: [],
        addDepartment: ''
      },
      AddRule: {
        addRealName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        addPhoneNumber: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^1[34578]\d{9}$/, message: '', trigger: 'blur' }
        ],
        addCode: [
          { required: true, message: '', trigger: 'change', type: 'array' }
        ],
        addDepartment: [
          { required: true, message: '', trigger: 'change', type: 'number' }
        ]
      },
      /*  */
      build: false
    }
  },
  props: {
    /*  */
    codeData: {},
    /*  */
    departmentList: {}
  },
  mounted () {
  },
  methods: {
    /* axios:  */
    sysAdd () {
      sysMobileUserAdd({
        accessToken: getToken(),
        validate: true,
        data: {
          createBy: this.$store.getters.getloginInfor().userId,
          realName: this.AddData.addRealName,
          phoneNumber: this.AddData.addPhoneNumber,
          code: this.AddData.addCode[this.AddData.addCode.length - 1].toString(),
          department: parseInt(this.AddData.addDepartment)
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.$emit('getTableData')
          this.$Notice.success({ title: '' })
          this.build = false
          this.AddData = {}
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '' })
      })
    },
    /*  */
    addSubmit () {
      this.$refs['add'].validate((valid) => {
        if (valid) {
          this.sysAdd()
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
</style>
